export default {
	data() {
		return {
			index: -1,
			picker: [{
				"id": "10001",
				"name": "北软泰嘉园1"
			}],
			list: [],
			tabCur: 0,
			mainCur: 0,
			verticalNavTop: 0,
			load: true,
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar,
			imgList: [],
			imgList2: [],
			modalName: null,
			upType: '',
			area: '',
			currentCommunity: '',
			showChose: false,
			showChose2: false,
			showProvince: true,
			showCommunity: true,
			showCity: false,
			showDistrict: false,
			showCityList: false,
			showDistrictList: false,
			province: 1,
			community: 1,
			city: 3,
			district: 57,
			GetProvinceId: 2,
			District: false,
			Province: false,
			Community: false,
			City: false,
			// v-for循环判断是否为当前
			selected: false,
			selectedC: false,
			info: [{
				id: 1,
				name: '1幢',
				city: [{
					id: 1,
					name: '1单元',
					district: [{
							id: 1,
							name: '101室'
						},
						{
							id: 2,
							name: '102室'
						},
						{
							id: 3,
							name: '103室'
						},
						{
							id: 4,
							name: '104室'
						}
					]
				}]
			}]
		};
	},
	onLoad() {
		uni.showLoading({
			title: '加载中...',
			mask: true
		});

	},
	created() {
		this.getAddress();
		this.getCommunityList();
	},
	onReady() {
		uni.hideLoading()
	},
	methods: {
		getAddress() {
			var self = this;
			const requestTask = uni.request({ //请求地址接口
				url: 'http://localhost:3000/address',
				success(res) {

					//转换成字符串
					var cityData = JSON.stringify(res.data);
					// 第二步用正则把这些相对应的关键字段转化成element ui  三级联动需要 的字段
					var options = JSON.parse(cityData.replace(/roomId/g, 'id').replace(/rName/g, 'name').replace(/roomList/g,
							'district')
						.replace(/buildId/g, 'id').replace(/buildName/g, 'name')
						.replace(/unitList/g, 'city').replace(/unitId/g, 'id').replace(/uName/g, 'name'));
					// console.log(options.data);
					self.info = options.data;
				}
			})
		},
		getCommunityList() {
			var self = this;
			const requestTask2 = uni.request({
				url: 'http://localhost:3000/communityList',
				success(res) {
					var communityDatas = JSON.stringify(res.data);
					var options = JSON.parse(communityDatas.replace(/commuId/g, 'id').replace(/commuName/g, 'name'));
					self.picker = options.communityList;
					console.log(options);
					console.log(self.picker);
				}
			})
		},
		choseAdd: function() {
			this.showChose = true;
		},
		closeAdd: function() {
			this.showChose = false;
		},
		choseAdd2: function() {
			this.showChose2 = true;
		},
		closeAdd2: function() {
			this.showChose2 = false;
		},
		_filter(add, name, code) {
			let result = [];
			for (let i = 0; i < add.length; i++) {
				if (code == add[i].id) {
					result = add[i][name];
				}
			}
			return result;
		},
		getProvinceId: function(code, input, index) {
			this.province = code;
			this.Province = input;
			this.showProvince = false;
			this.showCity = true;
			this.showDistrict = false;
			this.showCityList = this._filter(this.info, 'city', this.province);
			// 点击选择当前
			this.info.map(a => a.selected = false);
			this.info[index].selected = true;
			// this.$refs.selectUl.scrollTop = 0;
		},
		provinceSelected: function() {
			// 清除市级和区级列表
			this.showCityList = false;
			this.showDistrictList = false;
			// 清除市级和区级选项
			this.City = false;
			this.District = false;
			// 选项页面的切换
			this.showProvince = true;
			this.showCity = false;
			this.showDistrict = false;
		},
		getCommunityId: function(code, input, index) {
			this.community = code;
			this.Community = input;
			this.showCommunity = true;
			//点击选择当前的
			this.picker.map(a => a.selectedC = false);
			this.picker[index].selectedC = true;
		},
		btnSure(){
			this.currentCommunity = this.Community + ":" + this.community;
			console.log("当前选中：" + this.currentCommunity);
			this.showChose2 = false;
			this.sendValue(this.currentCommunity);
		},
		communitySelected: function() {
			this.showCommunity = true;
			this.Community = true;
		},
		getCityId: function(code, input, index) {
			this.city = code;
			this.City = input;
			this.showProvince = false;
			this.showCity = false;
			this.showDistrict = true;
			this.showDistrictList = this._filter(this.showCityList, 'district', this.city);
			// 选择当前添加active
			this.showCityList.map(a => a.selected = false);
			this.showCityList[index].selected = true;
			// this.$refs.selectUl.scrollTop = 0;
		},
		citySelected: function() {
			this.showProvince = false;
			this.showCity = true;
			this.showDistrict = false;
		},
		getDistrictId: function(code, input, index) {
			this.district = code;
			this.District = input;
			// 选择当前添加active
			this.showDistrictList.map(a => a.selected = false);
			this.showDistrictList[index].selected = true;
			this.area = this.Province + this.City + this.District /* + " : " + this.province + " : " + this.city + " : " + this.district */ ;
			// 选取市区选项之后关闭弹层
			this.showChose = false;
			this.sendValue(this.area);
			// this.$refs.selectUl.scrollTop = 0;
		},
		districtSelected: function() {
			this.showProvince = false;
			this.showCity = false;
			this.showDistrict = true;
		},
		//给父组件传值
		sendValue(val) {
			this.$emit('getValue', val);
		},
		PickerChange(e) {
			this.index = e.detail.value
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		ChooseImage() {
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['camera'], //从相册选择
				success: (res) => {

					if (this.imgList.length != 0) {
						this.imgList = this.imgList.concat(res.tempFilePaths)
					} else {
						this.imgList = res.tempFilePaths
					}
				}
			});
		},

		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {
			uni.showModal({
				title: '删除',
				content: '确定要删除此照片么吗？',
				cancelText: '取消',
				confirmText: '确认',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
		ChooseImage2() {
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					if (this.imgList2.length != 0) {
						this.imgList2 = this.imgList2.concat(res.tempFilePaths)
					} else {
						this.imgList2 = res.tempFilePaths
					}
				}
			});
		},
		ViewImage2(e) {
			uni.previewImage({
				urls: this.imgList2,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg2(e) {
			uni.showModal({
				title: '删除',
				content: '确定要删除此照片么吗？',
				cancelText: '取消',
				confirmText: '确认',
				success: res => {
					if (res.confirm) {
						this.imgList2.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		}
	}

}
